.coupon-card {
  display: flex;
  flex-direction: column;
  padding: 16px;
  background: #FFFFFF;
  border: 1px solid #e7e9eb;
  box-sizing: border-box;
  border-radius: 4px;
}
.coupon-card__copy {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  color: #AFAFB4;
  display: none;
  padding-left: 16px;
  background-image: url("data:image/svg+xml,%3Csvg%20width%3D%2213%22%20height%3D%2216%22%20viewBox%3D%220%200%2013%2016%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%3Cpath%20d%3D%22M1%2011.1654V2.83203C1%202.27975%201.44772%201.83203%202%201.83203H9%22%20stroke%3D%22%23AFAFB4%22%20stroke-width%3D%222%22%20stroke-linecap%3D%22round%22%2F%3E%0A%3Cpath%20d%3D%22M4%205.83203C4%205.27975%204.44772%204.83203%205%204.83203H11C11.5523%204.83203%2012%205.27975%2012%205.83203V13.832C12%2014.3843%2011.5523%2014.832%2011%2014.832H5C4.44772%2014.832%204%2014.3843%204%2013.832V5.83203Z%22%20stroke%3D%22%23AFAFB4%22%20stroke-width%3D%222%22%2F%3E%0A%3C%2Fsvg%3E%0A");
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
}
.coupon-card__copy::first-letter {
  text-transform: uppercase;
}
.coupon-card__copied {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  display: none;
  color: #26CB63;
}
.coupon-card__copied::first-letter {
  text-transform: uppercase;
}
.coupon-card__header {
  display: flex;
  align-items: center;
}
.coupon-card__description {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  margin: 12px 0 0 0;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.coupon-card__expired {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  margin-left: auto;
  color: #8E8FA0;
}
.coupon-card__coupon {
  display: flex;
  align-items: center;
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  margin-right: 12px;
  padding: 4px 8px;
  text-overflow: ellipsis;
  color: #005AFF;
  background: rgba(0, 90, 255, 0.08);
  border-radius: 2px;
}
.coupon-card__coupon::first-letter {
  text-transform: uppercase;
}
.coupon-card:hover {
  cursor: pointer;
  border: 1px solid #DADAE2;
}
.coupon-card:hover .coupon-card__copied,
.coupon-card:hover .coupon-card__copy {
  display: block;
}
