.content-coupons-list {
  display: grid;
  grid-template-rows: 64px fit-content(504px) 32px;
  position: fixed;
  z-index: 6000;
  top: 100px;
  right: 16px;
  max-height: 600px;
  width: 375px;
  background: #F7F7F7;
  box-shadow: 0 5px 6px rgba(0, 0, 0, 0.2), 0 3px 16px rgba(0, 0, 0, 0.12), 0 9px 12px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
}
.content-coupons-list__header {
  display: flex;
  justify-content: space-between;
  justify-items: center;
  align-content: center;
  background: #FFFFFF;
  box-shadow: 0 1px 0 #E7E9EB;
  padding: 12px;
  height: 64px;
}
.content-coupons-list__body {
  flex: 1 0 auto;
  max-height: 504px;
  height: 100%;
  overflow: auto;
}
.content-coupons-list__footer {
  display: flex;
  justify-items: center;
  align-items: center;
  justify-content: center;
  align-content: center;
  background: #FFFFFF;
  box-shadow: 0px -1px 0px #E7E9EB;
  height: 32px;
}
.content-coupons-list__footer-text {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 14px;
}
.content-coupons-list__footer-link {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
}
