.unsecure-notifications * {
  box-sizing: border-box;
}
.unsecure-notifications {
  background: #ffffff;
  box-shadow: 0px 5px 6px rgba(0, 0, 0, 0.2), 0px 3px 16px rgba(0, 0, 0, 0.12), 0px 9px 12px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  width: 640px;
  height: 437px;
  box-sizing: border-box;
  position: fixed;
  z-index: 2147483647;
  top: calc((100% - 437px) / 2);
  right: calc((100% - 640px) / 2);
}
.unsecure-notifications__wrapper {
  margin: 24px 0;
}
.unsecure-notifications__title {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  margin: 12px 56px 4px;
}
.unsecure-notifications__title::first-letter {
  text-transform: uppercase;
}
.unsecure-notifications__domain {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  margin: 4px 56px;
  color: #8E8FA0;
}
.unsecure-notifications__domain::first-letter {
  text-transform: uppercase;
}
.unsecure-notifications__text {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  background: #f7f7f7;
  border-radius: 12px;
  margin: 12px 98px 8px;
  padding: 12px 16px;
  text-align: center;
}
.unsecure-notifications__button {
  width: 280px;
  margin: 16px 0;
}
.unsecure-notifications__backdrop {
  position: fixed;
  z-index: 2147483646;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
  border-radius: 0 0 4px 4px;
}
