.base_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px;
  width: 100%;
  height: 100%;
}
.base_wrapper__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-grow: 0;
  height: 32px;
  width: 100%;
  margin: 0;
  padding: 0;
}
.base_wrapper__close {
  width: 14px;
  height: 14px;
  cursor: pointer;
}
.base_wrapper__content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}
