.caa-progress * {
  box-sizing: border-box;
}
.caa-progress {
  box-sizing: border-box;
  background: #ffffff;
  width: 640px;
  height: 368px;
  position: fixed;
  z-index: 2147483647;
  top: 150px;
  left: calc((100% - 640px) / 2);
  box-shadow: 0 5px 6px rgba(0, 0, 0, 0.2), 0 3px 16px rgba(0, 0, 0, 0.12), 0 9px 12px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
}
.caa-progress__logo {
  margin: 24px;
}
.caa-progress__text {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  margin: 24px 24px 12px;
  text-align: center;
}
.caa-progress__text::first-letter {
  text-transform: uppercase;
}
.caa-progress__decimal {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
}
.caa-progress__decimal::first-letter {
  text-transform: uppercase;
}
.caa-progress__fraction {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  font-size: 20px;
}
.caa-progress__fraction::first-letter {
  text-transform: uppercase;
}
.caa-progress__subtext {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  margin: 12px;
  text-align: center;
}
.caa-progress__progress-bar {
  height: 4px;
  width: 280px;
  margin: 12px;
  background: #e7e9eb;
  border-radius: 16px;
}
.caa-progress__progress-filter {
  width: 0;
  /*staring progress*/
  height: 100%;
  transition: width 0.2s ease-in;
  background: #005AFF;
  border-radius: 16px;
}
.caa-progress__button {
  margin: 12px;
  width: 280px;
}
.caa-progress__backdrop {
  position: fixed;
  z-index: 2147483646;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
  border-radius: 0 0 4px 4px;
}
