.caa-notification {
  background: #ffffff;
  width: 375px;
  height: 456px;
  display: flex;
  flex-direction: column;
  justify-items: flex-start;
  position: fixed;
  z-index: 2147483647;
  top: 24px;
  transition: 1s;
  box-shadow: 0 5px 6px rgba(0, 0, 0, 0.2), 0 3px 16px rgba(0, 0, 0, 0.12), 0 9px 12px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  box-sizing: border-box;
}
.caa-notification * {
  box-sizing: border-box;
}
.caa-notification--init {
  right: -500px;
}
.caa-notification--active {
  right: 24px;
}
.caa-notification--active {
  right: 24px;
}
.caa-notification__wrapper {
  margin-top: 32px;
}
.caa-notification__text {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  margin: 16px 0 0;
  text-align: center;
}
.caa-notification__text::first-letter {
  text-transform: uppercase;
}
.caa-notification__domain {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  margin: 8px;
  text-align: center;
}
.caa-notification__tooltip {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  background: #f7f7f7;
  border-radius: 12px;
  padding: 12px 16px;
  margin-top: 16px;
  margin-bottom: 8px;
  text-align: center;
}
