.offer-list {
  display: grid;
  grid-template-rows: repeat(40, fit-content(60px));
  grid-gap: 8px;
  padding: 16px;
  overflow: auto;
  width: 100%;
  height: 100%;
}
.offer-list::-webkit-scrollbar {
  width: 4px;
}
.offer-list::-webkit-scrollbar-thumb {
  background: #AFAFB4;
  border-radius: 12px;
}
.offer-list::-webkit-scrollbar-thumb:hover {
  background: lightgray;
}
.offer-list__title {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  color: #8E8FA0;
}
.offer-list__title::first-letter {
  text-transform: uppercase;
}
