.coupon-tooltip {
  position: relative;
  width: 100%;
  padding: 16px 24px;
  display: flex;
  align-items: center;
  background: #FCE3A4;
  border-radius: 4px;
  z-index: 2;
}
.coupon-tooltip__message {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  width: 100%;
  margin: 0 4px;
  display: flex;
  align-items: center;
  color: #2A2A4B;
}
.coupon-tooltip__message::first-letter {
  text-transform: uppercase;
}
