.caa-introduction {
  background: #ffffff;
  width: 375px;
  height: 478px;
  position: fixed;
  z-index: 2147483647;
  top: 24px;
  transition: 1s;
  box-shadow: 0 5px 6px rgba(0, 0, 0, 0.2), 0 3px 16px rgba(0, 0, 0, 0.12), 0 9px 12px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  box-sizing: border-box;
}
.caa-introduction--init {
  right: -500px;
}
.caa-introduction--active {
  right: 24px;
}
.caa-introduction__title {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  margin: 16px 24px 0;
}
.caa-introduction__title::first-letter {
  text-transform: uppercase;
}
.caa-introduction__text {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  margin: 8px 8px 16px;
}
.caa-introduction__terms {
  color: #505962;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  margin: 8px 8px 16px;
  color: #8E8FA0;
}
.caa-introduction__button {
  margin: 4px;
}
