.betternet_button {
  color: #2A2A4B;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  padding: 16px;
  box-sizing: border-box;
  border: none;
  border-radius: 100px;
  width: 215px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.betternet_button::first-letter {
  text-transform: uppercase;
}
.betternet_button:focus,
.betternet_button:active {
  outline: none;
}
.betternet_button--space-between {
  justify-content: space-between;
}
.betternet_button--pointer {
  cursor: pointer;
}
.betternet_button--full-width {
  width: 100%;
}
.betternet_button--transparent {
  background: transparent;
  color: #8E8FA0;
}
.betternet_button--blue {
  background: #005aff;
  color: #ffffff;
}
.betternet_button--blue:hover,
.betternet_button--blue:focus {
  background: #004bd5;
}
.betternet_button--light-grey {
  background: transparent;
  border: 2px solid #e7e9eb;
  color: #505962;
}
.betternet_button--light-grey:hover,
.betternet_button--light-grey:focus {
  background: #f7f7f7;
  border: 2px solid #e7e9eb;
  box-sizing: border-box;
  border-radius: 100px;
}
